@import url("https://fonts.googleapis.com/css2?family=Noto+Kufi+Arabic:wght@100;200;300;400;500&family=Roboto:wght@300&family=Source+Code+Pro:ital,wght@1,600&display=swap");
body {
  overflow-x: hidden;
  font-family: "Noto Kufi Arabic", sans-serif;
  background-color: #ebebeb;
}
body * {
  color: #3c3c3c;
  text-align: right;
  justify-content: space-around;
}

.bg-gray {
  background-color: #ebebeb !important;
}

.border-left-1 {
  border-top: 0 !important;
  border-bottom: 0 !important;
  border-right: 0 !important;
}

.border-right-1 {
  border-top: 0 !important;
  border-bottom: 0 !important;
  border-left: 0 !important;
}

.border-top-1 {
  border-left: 0 !important;
  border-right: 0 !important;
  border-bottom: 0 !important;
}

.border-bottom-1 {
  border-left: 0 !important;
  border-right: 0 !important;
  border-top: 0 !important;
}

.gap-0-5 {
  gap: 0.5rem;
}

.bg-gray {
  background-color: #ebebeb;
  color: #666565;
  font-weight: 700;
}

.bg-black {
  background-color: black !important;
  color: white !important;
  font-size: 1.3rem !important;
}

.section-design {
  padding: 0.5rem 1.8rem 0;
  margin-bottom: 1rem;
}

::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  margin-top: 0.2rem;
  border-radius: 50px;
  background-color: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.carousel-indicators li {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
}

.carousel-caption {
  position: absolute;
  height: 102px;
  width: 100%;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
}
.carousel-caption p {
  text-align: center;
  font-size: 1.2rem;
  color: white;
}

.news-paper_title {
  width: fit-content;
  background-color: #ebebeb;
  color: #cf2828;
  padding: 0 0.4rem 0 0.8rem;
  border-right: 0.3rem solid #cf2828 !important;
}

.card {
  border: none;
}

.card-title {
  font-size: 1rem;
  font-weight: 700;
  color: #666565;
}

.card-header {
  background-color: #afaeae;
  font-weight: 600 !important;
  color: #424141;
  min-height: 52px;
}

.text-black {
  color: black !important;
}

.play {
  position: absolute;
  z-index: 1;
  left: 1rem;
  top: 10px;
  border: 3px solid white;
  border-radius: 10%;
  display: flex;
}
.play i {
  font-size: 14px;
  color: white;
}
.play-small {
  padding: 0.2rem 0.6rem;
}
.play-small i {
  font-size: 8px;
}
.play-mid {
  padding: 0.4rem 0.8rem;
}
.play-mid i {
  font-size: 12px;
}

.pictures {
  position: absolute;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  top: 1.5rem;
}

.btn {
  background-color: white;
  border-radius: 10%/50%;
  color: #666565;
  margin-left: -1rem;
  margin-bottom: -0.2rem;
  margin-right: 2rem;
}

.jumbotron {
  padding: 7px 0 1px;
  margin-top: 46px;
  margin-bottom: 13px;
}
.jumbotron .nav-link {
  padding: 0;
  text-transform: capitalize;
}
.jumbotron .lang {
  font-size: 1rem;
  text-transform: uppercase;
}
.jumbotron .nav-item {
  padding: 0;
  margin: 0 0.5rem;
  position: relative;
}
.jumbotron .nav-item a {
  padding: 0;
  padding-right: 1rem;
  font-size: 11.5px;
  font-weight: 500;
  text-transform: uppercase;
}
.jumbotron .btn {
  border: 1px solid #c9c9c9 !important;
  border-radius: 10%/50% !important;
  margin-bottom: -2px;
}
.jumbotron .btn a {
  margin: 1px 0;
  padding: 0 0.7rem;
}
.jumbotron .btn a i {
  margin-left: 2px;
  color: #848388;
}

.sm-border {
  position: absolute;
  top: 20%;
  width: 1px;
  height: 9px;
  background-color: #c9c9c9;
}

.nav-section {
  padding: 20px 0;
}
.nav-section * {
  color: #b40031;
  font-weight: 600;
}
.nav-section i {
  font-size: 1.4rem;
}
.nav-section .nav-item {
  padding: 0;
}
.nav-section .nav-item a {
  padding: 0;
  margin: 0;
}
.nav-section .search {
  position: relative;
}
.nav-section .search .sm-border {
  height: 40px;
  left: 50px;
  top: -10px;
}

.active {
  color: #70737a !important;
}

.social-media-links {
  position: absolute;
  left: 10rem;
  top: 30%;
}
.social-media-links .fab,
.social-media-links .fa {
  font-size: 15px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
  border-radius: 50%;
  color: white !important;
}
.social-media-links .fab:hover {
  opacity: 0.7;
}
.social-media-links .fa-facebook-f {
  background: #3b5998;
  color: white;
}
.social-media-links .fa-twitter {
  background: #55acee;
  color: white;
}
.social-media-links .fa-google {
  background: #dd4b39;
  color: white;
}
.social-media-links .fa-linkedin {
  background: #007bb5;
  color: white;
}
.social-media-links .fa-youtube {
  background: #bb0000;
  color: white;
}
.social-media-links .fa-instagram {
  background: #125688;
  color: white;
}
.social-media-links .fa-pinterest {
  background: #cb2027;
  color: white;
}
.social-media-links .fa-snapchat-ghost {
  background: #fffc00;
  color: white;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.social-media-links .fa-skype {
  background: #00aff0;
  color: white;
}
.social-media-links .fa-android {
  background: #a4c639;
  color: white;
}
.social-media-links .fa-dribbble {
  background: #ea4c89;
  color: white;
}
.social-media-links .fa-vimeo {
  background: #45bbff;
  color: white;
}
.social-media-links .fa-tumblr {
  background: #2c4762;
  color: white;
}
.social-media-links .fa-vine {
  background: #00b489;
  color: white;
}
.social-media-links .fa-foursquare {
  background: #45bbff;
  color: white;
}
.social-media-links .fa-stumbleupon {
  background: #eb4924;
  color: white;
}
.social-media-links .fa-flickr {
  background: #f40083;
  color: white;
}
.social-media-links .fa-yahoo {
  background: #430297;
  color: white;
}
.social-media-links .fa-soundcloud {
  background: #ff5500;
  color: white;
}
.social-media-links .fa-reddit {
  background: #ff5700;
  color: white;
}
.social-media-links .fa-rss {
  background: #ff6600;
  color: white;
}

.news-first-section .card-header {
  font-size: 0.9rem;
  font-weight: 500;
  padding: 20px 0;
  margin: 0;
}
.news-first-section .card-header .small-box {
  width: 21px;
  height: 13px;
  background-color: #c7c7c7;
  margin-left: 12px;
  margin-top: 0.2rem;
}
.news-first-section .card-header ul a {
  font-size: 1.1rem;
  padding: 0.3rem 0.7rem;
  background-color: #fbfbfb;
  border: 1px solid #c9c9c9;
}
.news-first-section .row {
  margin-top: 2rem;
}
.news-first-section .row .carousel .carousel-item {
  height: 100%;
}
.news-first-section .row .carousel .carousel-item img {
  height: 645px;
}
.news-first-section .row .carousel .arrow .btn {
  background-color: rgba(128, 128, 128, 0.6);
  border: 1px solid white;
  margin-left: 35px;
}
.news-first-section .row .carousel .arrow .btn i {
  color: white;
  font-size: 1.4rem;
  padding: 0.4rem 0.2rem;
}
.news-first-section .row .card {
  border: 0;
  height: 645px;
}
.news-first-section .row .card * {
  border: 0;
}
.news-first-section .row .card .list-group {
  font-size: 0.8rem;
  border-radius: 0;
  height: 100%;
  overflow-y: hidden;
}
.news-first-section .row .card .list-group-item {
  border-bottom: 0.1rem solid #ebebeb;
  padding-right: 2rem !important;
  padding-left: 0.5rem !important;
  position: relative;
  margin: 0 !important;
}
.news-first-section .row .card .list-group-item-action {
  background-color: transparent !important;
}
.news-first-section .row .card .list-group-item:hover {
  background-color: rgba(112, 115, 122, 0.2);
}
.news-first-section .row .card .list-group-item .news-paper_title {
  margin-right: 1.4rem;
  margin-top: -0.1rem;
}
.news-first-section .row .card .list-group-item::after {
  content: "";
  position: absolute;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #a2dfa2;
  top: 1rem;
  right: 0.56rem;
}
.news-first-section .row .card .list-group-item::before {
  content: "";
  position: absolute;
  width: 0.1rem;
  height: 100%;
  background-color: #ebebeb;
  right: 1rem;
}
.news-first-section .news {
  font-weight: 500;
}
.news-first-section .news small {
  font-size: 12px;
}
.news-first-section .news .active p {
  color: red !important;
}
.news-first-section .news .active .news-paper_title {
  background-color: #01655b;
  color: white !important;
  border-color: #01655b !important;
}
.news-first-section .news .active {
  background-color: rgba(112, 115, 122, 0.1) !important;
}
.news-first-section .carousel-caption {
  height: 32%;
}

.news-second-section {
  margin-top: 30px;
}
.news-second-section .row .card {
  border: none;
  position: relative;
}
.news-second-section .row .card img {
  height: 100%;
}
.news-second-section .row .card .card-body {
  text-align: right;
  position: absolute;
  height: 35%;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  line-height: 0.2;
}
.news-second-section .row .card .card-body * {
  color: white !important;
}
.news-second-section .row .card-deck .card {
  margin: 0;
  margin-left: 15px;
}

.news-third-section {
  margin-top: 30px;
  padding: 0;
  counter-reset: counter;
}
.news-third-section * {
  box-sizing: border-box;
}
.news-third-section .news .card {
  padding: 30px 0;
  margin: 0;
  border: 0;
  height: 200px;
}
.news-third-section .news .card .img {
  width: 207px;
}
.news-third-section .news .card .img img {
  width: 230px;
  height: 150px;
}
.news-third-section .news .card .img + .card-body {
  padding: 30px 10px 30px 60px;
  margin-right: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.news-third-section .news .card .card-text {
  font-size: 0.9rem;
  font-weight: 200;
}
.news-third-section .studies {
  padding-bottom: 5px !important;
}
.news-third-section .studies * {
  border: 0;
  border-radius: 0;
}
.news-third-section .studies .card-header {
  font-size: 1.1rem !important;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.news-third-section .studies .list-group-item {
  font-size: 0.9rem;
}
@media screen and (max-width: 1608px) {
  .news-third-section .studies .list-group-item h5 {
    font-size: 0.9rem !important;
  }
  .news-third-section .studies .list-group-item p {
    font: 2px !important;
  }
}
.news-third-section .studies .first {
  height: 515px;
}
.news-third-section .studies .first .card-header {
  background-color: #677497 !important;
}
.news-third-section .studies .first .list-group {
  height: 463px;
  overflow-y: scroll;
  background-color: rgba(103, 116, 151, 0.3);
}
.news-third-section .studies .first .list-group-item {
  background-color: rgba(103, 116, 151, 0.3);
}
.news-third-section .studies .first .list-group-item h5 {
  font-size: 1rem;
  padding: 0.43rem 0;
}
.news-third-section .studies .second {
  height: 515px;
}
.news-third-section .studies .second .card-header {
  background-color: #65a1d3 !important;
}
.news-third-section .studies .second .list-group {
  background-color: rgba(101, 161, 211, 0.3) !important;
  overflow-y: scroll;
}
.news-third-section .studies .second .list-group .list-group-item {
  background-color: rgba(101, 161, 211, 0.3) !important;
  padding: 1.2rem 2rem;
  border-bottom: 1px solid rgba(128, 128, 128, 0.342);
}
.news-third-section .studies .second .list-group .ordering::before {
  counter-increment: counter;
  content: counter(counter);
  position: absolute;
  width: 2rem;
  height: 2rem;
  font-size: 1.4rem;
  font-weight: 800;
  margin-left: 2rem;
  top: 12%;
  right: 0.5rem;
  color: rgba(66, 65, 65, 0.2);
}
.news-third-section .social-media * {
  font-size: 1rem;
}
.news-third-section .social-media-twitter .list-group {
  border-radius: 0;
  max-height: 400px;
  border-left: 1px solid;
  overflow-x: hidden;
  overflow-y: scroll;
  text-align: left;
}
.news-third-section .social-media-twitter .list-group-item {
  border: none;
  border-bottom: 1px solid #3c3c3c !important;
  display: flex;
  justify-content: flex-end;
}
.news-third-section .social-media-twitter .list-group-item .card-group {
  display: flex;
  flex-direction: column;
  width: 92%;
}
.news-third-section .social-media-twitter .list-group-item .card-group * {
  text-align: left;
}
.news-third-section .social-media-twitter .list-group-item img {
  border-radius: 3% 3% 0 0/4%;
}
.news-third-section .social-media-twitter .list-group-item .item-heading {
  text-align: right;
  margin-top: -10px;
}
.news-third-section .social-media-twitter .twitter-identification {
  padding-right: 10px;
}
.news-third-section .social-media-twitter .twitter-identification > div {
  flex-direction: row-reverse;
}
.news-third-section .social-media-twitter .twitter-content,
.news-third-section .social-media-twitter .card-text {
  margin-bottom: 10px;
  overflow: hidden;
  display: -webkit-box !important;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  white-space: normal;
}
.news-third-section .social-media-instagram .card-group {
  padding: 0;
}
.news-third-section .social-media-instagram .card-group img {
  padding: 2px;
  min-height: 138px;
}

.news-fourth-section .card-header {
  font-size: 1rem;
  font-weight: 600;
  justify-content: flex-start;
}
.news-fourth-section .card-header i {
  margin-left: 6px;
  color: #fcba0c;
}
.news-fourth-section .nav[role=tablist] {
  padding: 0;
  margin: 0;
}
.news-fourth-section .nav[role=tablist] a {
  margin-left: 3rem;
  color: inherit;
}
.news-fourth-section a[aria-selected=true] {
  border-bottom: 0.13rem solid #2e2c2c;
}
.news-fourth-section .card-body {
  text-align: right;
  position: absolute;
  width: 100%;
  height: 27%;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
}
.news-fourth-section .card-body * {
  color: white !important;
}

.news-fifth-section {
  margin-top: 30px;
}
.news-fifth-section .carousel {
  padding: 0;
  height: 100%;
}

.arrow-up {
  margin: 25px 0;
}
.arrow-up .btn {
  background-color: rgba(255, 255, 255, 0.6);
  border: 2px solid rgba(175, 174, 174, 0.8);
  font-size: 20px;
  font-weight: 100;
}
.arrow-up .btn i {
  padding: 0 3px;
  color: rgba(60, 60, 60, 0.7);
}

.footer {
  border-top: 3px solid rgba(175, 174, 174, 0.5);
  height: 40rem;
}
.footer * {
  color: rgba(60, 60, 60, 0.8);
}
.footer-element .col-4 {
  display: flex;
}
.footer .list-group {
  padding: 0;
  margin-top: 1.2rem;
  display: inline-block;
}
.footer .list-group-item {
  font-size: 0.9rem;
  font-weight: 700;
  border: none;
  padding: 0;
}
.footer .list-group-item a {
  color: black;
}
.footer-contact {
  font-weight: 600;
  font-size: 0.8rem;
}
.footer-contact a {
  position: relative;
}
.footer-contact .sm-border {
  left: 0;
  top: 35%;
}

.list-group {
  display: inline-block;
}

